<template>
    <div class="chart-wrapper" ref="chart" >
    </div>
</template>

<script type="text/javascript" scoped>
  export default {
    name: 'home',
    props: {
      data: {
        type: Object,
        default: () => {}
      },
    },
    data () {
      return {}
    },
    mounted() {
      this.initOption();
    },
    methods: {
      initOption() {
        // initOption 作用： 一次性捕获 echats 图表的挂载点，声明好 echats 图表的 基本配置
        this.chart = this.$echarts.init(this.$refs.chart);
        this.option = {
          backgroundColor: '#012422',
          color: ['#18606e'],
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
              type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            },
            formatter: function (params, ticket, callback) {
              return "<div style='text-align: left'>"+params[0].name+"年<br>建设投入"+"：<span style='color:#4cd5ff'>"+params[0].value+"(千万元)</span><br>同比增长率"+"：<span style='color:#4cd5ff'>"+params[1].value+"%</span></div>";
            }
          },
          grid: {
            right: '7%',
            left: '7%',
            bottom: '17%',
            top: '25%'
          },
          legend: {
            show: true,
            y:'top',
            x: 'center',
            height:10,
            itemWidth:10,
            itemHeight:10,
            padding:[10,0,0,0],
            data: [{
              name: '建设资金投入',
              icon: 'rect',
            },
              {
              name: '同比增长率',
              icon: 'circle',
            }],
            textStyle: {
              color:'#fff'
            }
          },
          xAxis : [
            {
              type : 'category',
              data :['月湖区', '信江新区', '贵溪市', '余江县'],
              axisTick: {
                show: false
              },
              axisLabel: {
                show:true,
                color: '#fff',
                fontSize: 12,
                top:0
              },
              axisLine: {
                lineStyle: {
                  color: '#124886',
                }
              },
              splitLine: {
                show:false,
              },

            }
          ],
          yAxis : [
            {
              type : 'value',
              name: '资金（千万元）',
              min:0,
              max:10,
              interval: 2,
              nameTextStyle:{
                color:'#99ffcc',
                fontSize: 12,
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                show:true,
                fontSize: 12,
                color: '#fff'
              },
              splitLine: {
                show:false,
              },
              axisLine: {
                lineStyle: {
                  color: '#124886'
                }
              },
            },
            {
              type: 'value',
              name: '同比增长率(%)',
              min: 0,
              max: 10,
              interval: 2,
              nameTextStyle:{
                color:'#99ffcc',
                fontSize: 12,
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                show:true,
                formatter: '{value} %',
                fontSize: 12,
                color: '#fff'
              },
              splitLine: {
                show:false,
              },
              axisLine: {
                lineStyle: {
                  color: '#124886'
                }
              },
            }
          ],
        dataZoom: [
            {
                type: 'inside',
                xAxisIndex: [0],
                start: 0,
                end: 100
            },
            {
                bottom: 2,
                type: 'slider',
                show: true,
                xAxisIndex: [0],
                start: 0,
                end: 100,
                fillerColor: '#2cc0b6',
                backgroundColor: '#002624',
                height: 5,
                borderRadius: '7px',
                borderColor: '#23a199',
                textStyle: {
                    color: 'transparent'
                },
                handleSize: '150%',
                handleStyle: {
                    color: 'transparent'
                }
            }
        ],
          series : [
            {
              name: '建设资金投入',
              type: 'bar',
              stack: '万亩',
              barWidth : 15,
              itemStyle: {
                normal: {
                  borderColor: '#00daaf',
                  borderWidth: 1,
                }
              },
              label: {
                normal: {
                  show: false
                }
              },
              data: [3, 7, 4, 3.5]
            },
            {
              name: '同比增长率',
              type: 'line',
              yAxisIndex: 1,
              symbol: 'circle',
              lineStyle: {
                normal: {
                  color: '#eda402'
                }
              },
              itemStyle: {
                normal: {
                  color: '#ff8212',
                  borderColor: '#eccf3e',
                  borderWidth: '2',
                },
              },

              data:[3, 7, 4, 3.5],
              symbolSize: 5
            }
          ]
        };
        this.chart.setOption(this.option, true);
        let self=this;
        window.addEventListener('resize', function () {
          self.chart.resize();
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .chart-wrapper{
    width: 100%;
    height: 100%;
  }
</style>



