<template>
  <div class="chart-box" ref="chart1"></div>

</template>

<script>
  import {echartsMixin} from 'assets/js/common/mixin.js'
  import {dataZoom} from 'assets/js/common/echartsComm.js';

  export default {
    name: "quantity",
    mixins: [echartsMixin],
    component: {},
    data() {
      return {
        chart1: '',
        echartsData: {
          COD: [40, 10, 20, 20, 41, 64, 60],
          BODS: [45, 40, 20, 47, 20, 80, 70],
          TN: [20, 46, 40, 60, 40, 43, 13],
          TP: [25, 70, 24, 20, 56, 34, 40],
          SS: [56, 20, 24, 62, 30, 50, 60],
          AN: [34, 10, 35, 40, 11, 20, 24],
          time: ["2月24日", "2月25日", "2月26日", "2月27日", "2月28日", "3月1日", "3月2日"],
        },
      }
    },
    mounted() {
      this.chart1 = this.$echarts.init(this.$refs.chart1)
      this.initOption()
    },
    methods: {
      initOption() {
        let option1 = {
          color: ["#E3CA04", "#EC7823", "#4BE431", "#29F3D9", "#298DF3", "#EC2344"],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              width: "30%",
              type: 'shadow',
              shadowStyle: {
                color: "rgba(8,104,98,0.36)"
              }
            },
            backgroundColor: "#002624",
            borderColor: '#009288',
            borderWidth: 1,
            textStyle: {
              color: "#fff",
            },
            formatter: (params) => {
              let html = "<div style='text-align:center;padding:15px'>" + params[0].name + "<br/>"
              params.forEach((val, index) => {
                html += `${val.seriesName}：<span style="color:#4CD5FF">${val.data}</span><br/>`

              })
              return html + '</div>'
            }
          },
          grid: {
            left: '20',
            right: '40',
            bottom: '40',
            top: "40",
            containLabel: true
          },
          legend: {
            top: '10',
            data: ['COD', 'BODS', 'TN', 'TP', 'SS', '氨氮'],
            itemHeight: 3,
            itemWidth: 20,
            icon: 'rect',
            textStyle: {
              color: '#fff'
            },
          },
          xAxis: {
            type: 'category',
            name: '日期',
            nameTextStyle: {
              color: '#99ffcc',
              fontWeight: 'bold',
            },
            nameGap: '5',
            boundaryGap: true, //坐标轴的留白
            axisLine: {
              show: true,
              lineStyle: {
                color: "#00C3D3",
              }
            },
            axisTick: {
              show: false //坐标轴刻度
            },
            axisLabel: {
              color: '#fff',
              fontSize: 12,
              textStyle: {},
            },
            data: this.echartsData.time
          },
          yAxis: {
            type: 'value',
            name: '单位（mg/L）',
            nameTextStyle: {
              color: '#99ffcc',
              fontWeight: 'bold',
              padding: [0, 0, 0, 20],
            },
            axisTick: {
              show: false //坐标轴刻度
            },
            axisLine: {
              lineStyle: {
                color: "#35ece6",
                type: "solid",
                opacity: 1
              }
            },
            axisLabel: {
              color: '#fff',
              fontSize: 12,
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#0C615B',
                type: "solid",

              }
            },
          },
          series: [{
            name: 'COD',
            type: 'line',
            showSymbol: false,
            lineStyle: {
              type: 'solid',
            },
            itemStyle: {
              normal: {},
            },
            data: this.echartsData.COD
          }, {
            name: 'BODS',
            type: 'line',
            showSymbol: false,
            lineStyle: {
              type: 'solid',
            },
            itemStyle: {},
            data: this.echartsData.BODS
          }, {
            name: 'TN',
            type: 'line',
            showSymbol: false,
            lineStyle: {
              type: 'solid',
            },
            itemStyle: {},
            data: this.echartsData.TN
          }, {
            name: 'TP',
            type: 'line',
            showSymbol: false,
            lineStyle: {
              type: 'solid',
            },
            itemStyle: {},
            data: this.echartsData.TP
          }, {
            name: 'SS',
            type: 'line',
            showSymbol: false,
            lineStyle: {
              type: 'solid',
            },
            itemStyle: {},
            data: this.echartsData.SS
          }, {
            name: '氨氮',
            type: 'line',
            showSymbol: false,
            lineStyle: {
              type: 'solid',
            },
            itemStyle: {},
            data: this.echartsData.AN
          }],
          dataZoom
        }
        this.chart1.setOption(option1)
      },
      _resizeHanlder() {
        this.chart1.resize()
      },
      _destroyHandler() {
        if (this.chart1) {
          this.chart1.dispose()
        }
      }
    }
  }
</script>

<style lang="scss" scoped>

  .chart-box {
    width: 100%;
    height: calc(100% - 30px);

  }
</style>



